import React, { useState } from 'react';
import FAQList from '../FAQList';

import './styles.css';
import './responsive.css';


export default function FAQSection({ certification }) {
  const [faqs, setFaqs] = useState([
    {
      question: '1. Qual certificao o aluno recebe ao final do curso?',
      answer: `O aluno receber um certificado de participao a cada mdulo concludo e um certificado de formao em ${certification} ao final da programao completa.`,
      open: false
    },
    {
      question: '2. Os cursos de UCAM so reconhecidos pelo MEC?',
      answer: 'Os cursos UCAM so Bootcamps, um programa de ensino intenso e imersivo, que foge da metodologia formal e  focado nas prticas mais relevantes para atuar no mercado de trabalho.',
      open: false
    },
    {
      question: '3. Alunos que ainda no concluiram a formao podem realizar os cursos?',
      answer: 'Sim  possvel, pois no  necessrio ter uma formao prvia para realizao do curso, por se tratar de um curso livre de capacitao.',
      open: false
    },
    {
      question: '4. Qual a forma de avaliao das disciplinas?',
      answer: 'As avaliaes sero feitas por meio de atividades online ao longo das matrias.',
      open: false
    },
    {
      question: '5. Ex-Alunos da UCAM tm desconto?',
      answer: 'Sim, alunos e ex alunos tm desconto de 15%',
      open: false
    }
  ]);

  const toggleFAQ = index => {
    setFaqs(faqs.map((faq, i) => {

      if (i === index){
        faq.open = !faq.open
      }else {
        faq.open = false;
      }

      return faq;
    }))
  }

  return (

    <div className="container-faq-section">
      <h2>FAQ</h2>
      <div className="container-faqs">
        {faqs.map((faq, index) => (
            
            <FAQList 
              key={index}
              faq={faq}
              index={index}
              toggleFAQ={toggleFAQ}
            />

          ))}
      </div>
    </div>

  )
}
